const CustomAccordionSummary = styled(AccordionSummary)(() => ({
  backgroundColor: colors.background,
  color: colors.primary.active,
  minHeight: 0,
  "&.Mui-expanded": {
    minHeight: 0,
    "& .MuiAccordionSummary-content": {
      margin: 8,
    },
  },
  "& .MuiAccordionSummary-content": {
    margin: 8,
  },
}));

const CustomAccordionDetails = styled(AccordionDetails)(() => ({
  backgroundColor: colors.body,
  color: colors.primary.active,
}));

interface AccordionItemProps {
  title: string;
  content: string;
}

const AccordionItem: React.FC<AccordionItemProps> = ({ title, content }) => (
  <Accordion sx={{ "&.Mui-expanded": { margin: 0 } }}>
    <CustomAccordionSummary aria-controls="panel1-content" id="panel1-header">
      <Typography sx={{ width: "100%", textAlign: "center", fontSize: 14 }}>
        {title}
      </Typography>
    </CustomAccordionSummary>
    <CustomAccordionDetails>
      <Typography sx={{ width: "100%", fontSize: 14 }}>{content}</Typography>
    </CustomAccordionDetails>
  </Accordion>
);

interface FAQListProps {
  data: AccordionItemProps[];
  lang: string;
}

const FAQList: React.FC<FAQListProps> = ({ data, lang }) => (
 
  <div className={`${getFontClass(`${lang}_m`)} p-4`}>
    {data.map((item, index) => (
      <AccordionItem key={index} {...item} />
    ))}
  </div>
);

